/*  */
/* ROOT VARIABLES - CYBERPUNK 2045 */
/*  */

:root {
  /* Colors - Cyberpunk Palette */
  --color-primary: #00f0ff;
  --color-secondary: #c026d3;
  --color-accent: #ec4899;
  --color-success: #10b981;
  --color-warning: #f59e0b;
  --color-danger: #ef4444;
  --color-info: #3b82f6;
  
  /* Dark Theme */
  --bg-dark: #000000;
  --bg-darker: #0a0a0a;
  --bg-card: rgba(10, 10, 10, 0.7);
  --bg-glass: rgba(10, 10, 10, 0.5);
  
  /* Text */
  --text-primary: #ffffff;
  --text-secondary: #a0aec0;
  --text-tertiary: #718096;
  
  /* Borders */
  --border-light: rgba(0, 240, 255, 0.3);
  --border-medium: rgba(0, 240, 255, 0.5);
  --border-heavy: rgba(0, 240, 255, 0.8);
  
  /* Effects */
  --blur-glass: blur(10px);
  --blur-heavy: blur(20px);
  --glow-cyan: 0 0 30px rgba(0, 240, 255, 0.3);
  --glow-purple: 0 0 30px rgba(192, 38, 211, 0.3);
  --glow-pink: 0 0 30px rgba(236, 72, 153, 0.3);
}

/*  */
/* GLOBAL STYLES */
/*  */

html {
  scroll-behavior: smooth;
}

body{
  --tw-bg-opacity: 1;
  background-color: rgb(0 0 0 / var(--tw-bg-opacity, 1));
  font-family: 'Inter';
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity, 1));
  background: 
    radial-gradient(circle at 20% 50%, rgba(0, 240, 255, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(192, 38, 211, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 50% 0%, rgba(236, 72, 153, 0.1) 0%, transparent 50%),
    #000000;
  background-attachment: fixed;
  overflow-x: hidden;
}

/*  */
/* GLASSMORPHISM COMPONENTS */
/*  */

